@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

html {
  font-size: 62.5%;
  scroll-behavior: smooth;

  @media screen and (max-width: 1020px) {
    font-size: 60%;
  }

  @media screen and (max-width: 540px) {
    font-size: 45%;
  }
}

body {
  min-width: 32rem;
  font-family: 'Noto Sans KR', sans-serif;
}

ul, ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,time,figure,article,nav,header,footer,hgroup,video,audio,aside,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,font,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td {
  margin: 0;
  padding: 0;
  border: 0;
  font-weight: inherit;
  font-style: inherit;
  vertical-align: baseline;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body { line-height: 1; word-break: keep-all; word-wrap: break-word;}
dl,ul,ol,menu,li {list-style: none;}
fieldset,img{ border: 0 none; -webkit-box-sizing: border-box; box-sizing: border-box; }
table { width: 100%; table-layout: fixed; border-collapse: collapse; border-spacing: 0; }
table caption { width:0; height:0; visibility:hidden; text-indent:-9999px; font-size:0; line-height:0; }
caption,th,td {text-align: left;font-weight: normal;}
figure,article,header,footer,nav,hgroup,video,audio,aside,main {display: block;}
a { color:inherit; text-decoration:none; }
a:active, a:hover, a:focus { text-decoration:none; }
address, caption, cite, code, dfn, em, var { font-style: normal; font-weight: normal; -webkit-box-sizing: border-box; box-sizing: border-box; }
input, select, textarea, button { vertical-align: middle; color: inherit; resize:none; }
